import React, { useState } from 'react';
import membersData from './assets/members.json'; // 
import './Members.css';

const Members: React.FC = () => {
  const [selectedMember, setSelectedMember] = useState<any | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false); // 

  // 
  const handleImageClick = (member: any) => {
    setSelectedMember(member);
    setIsModalOpen(true); // 
  };

  // 
  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedMember(null);
  };

  return (
    <div>
      <h1></h1>
      <div className='Introduction'>
      SEVENTEENPledis13<br />
      Hiphop TeamVocal TeamPerformance Team<br />
      BSSJxW<br />
      13+3+1
      </div>
      <ul className="card-container">
        {membersData.map((member) => (
          <li key={member.id} className="card" style={{ display: 'flex', alignItems: 'center' }}>
            <img
              src={require(`${member.photo}`)}
              alt={member.name}
              className="member-image"
              onClick={() => handleImageClick(member)} // 
            />
            <div className="card-name">{member.name}</div> {/*  */}
          </li>
        ))}
      </ul>
      <div></div>

      {/*  */}
      {isModalOpen && selectedMember && (
        <div className="modal-overlay" onClick={closeModal}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-body">
              <img
                src={require(`${selectedMember.photo}`)}
                alt={selectedMember.name}
                className="modal-image"
              />
              <div className="modal-info">
                <h2>{selectedMember.name}</h2>
                <p>: {selectedMember.team}</p>
                <p>: {selectedMember.birthday}</p>
                <p>: {selectedMember.nationality}</p>
                <button className="close-modal" onClick={closeModal}></button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Members;
